<template>
  <v-container>
    <v-row>
      <v-col>
        <v-card>
          <v-card-title>管理员面板：管理控制</v-card-title>
          <v-card-subtitle style="margin-top: 1vh;">欢迎，管理员{{supervisor}}!</v-card-subtitle>

          <!--          管理面板切换-->
          <v-tabs
              fixed-tabs
              background-color="indigo"
              dark
          >
            <v-spacer></v-spacer>
            <v-tab to='/universityTable'>
              院校信息管理
            </v-tab>
            <v-tab @click="getStudent" to='/layout/TestPage'>
              学生账户管理
            </v-tab>
            <v-tab to='/teacherTable'>
              教师账户管理
            </v-tab>
            <v-tab to='/adminTable'>
              管理员账户管理
            </v-tab>
            <v-tab to='/uploadTable'>
              Excel文件上传
            </v-tab>
          </v-tabs>

          <v-btn class="blue white--text" style="margin-top: 1vh; margin-bottom: 1vh; width: 100%">欢迎！</v-btn>
        </v-card>


      </v-col>

    </v-row>


    <!--    表格部分数据-->
    <v-row>
      <v-col>
        <v-data-table
            :headers="headers"
            :items="stdData"
            sort-by="class"
            class="elevation-1"
        >
          <template v-slot:top>
            <v-toolbar
                flat
            >
              <v-toolbar-title>{{choosedTitle}}</v-toolbar-title>
              <v-divider
                  class="mx-4"
                  inset
                  vertical
              ></v-divider>
              <v-spacer></v-spacer>
              <v-dialog
                  v-model="dialog"
                  max-width="500px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                      color="success"
                      dark
                      class="mb-2"
                      style="margin-left: 1vh;margin-right: 1vh"
                      @click="getStudent"
                  >刷新</v-btn>
                  <v-btn
                      color="indigo"
                      dark
                      class="mb-2"
                      v-bind="attrs"
                      v-on="on"
                  >
                    新建
                  </v-btn>

                </template>
                <v-card>
                  <v-card-title>
                    <span class="text-h5">{{ formTitle }}</span>
                  </v-card-title>

                  <!--                  新增数据的验证逻辑-->
                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <v-text-field
                              v-model="editedItem.adminID"
                              label="管理员账号"
                          ></v-text-field>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <v-text-field
                              v-model="editedItem.name"
                              label="管理员姓名"
                          ></v-text-field>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <v-text-field
                              v-model="editedItem.passwd"
                              label="密码"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="close"
                    >
                      取消
                    </v-btn>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="save"
                    >
                      保存
                    </v-btn>
                  </v-card-actions>
                </v-card>


              </v-dialog>

              <v-dialog v-model="dialogDelete" max-width="20%">
                <v-card>
                  <v-card-title class="text-h3 red--text text-center" style="margin-bottom: 1.5vh">是否删除该管理员数据？</v-card-title>
                  <v-divider></v-divider>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" text @click="deleteItemConfirm">确定</v-btn>
                    <v-btn color="blue darken-1" text @click="closeDelete">取消</v-btn>
                    <v-spacer></v-spacer>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>

          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon
                small
                class="mr-2"
                @click="editItem(item)"
            >
              mdi-pencil
            </v-icon>
            <v-icon
                small
                @click="deleteItem(item)"
            >
              mdi-delete
            </v-icon>
          </template>
          <template v-slot:no-data>
            <v-btn
                color="primary"
                @click="initialize"
            >
              Reset
            </v-btn>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
    <!--          -->

  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: 'Contacts',
  data: () => ({

    snack:true,
    //界面数据
    supervisor:'',

    stdGet:'',
    stdUpdate:'',
    stdDelete:'',
    stdRegister:'',

    selectPage:'',
    choosedTitle:'管理员信息管理',

    //账号<20 8<=密码<20
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: '管理员账号',
        align: 'start',
        sortable: false,
        value: 'adminID',
      },
      { text: '管理员姓名', value: 'name' },
      { text: '修改', value: 'actions', sortable: false },
    ],
    stdData: [],
    editedIndex: -1,
    editedItem: {
      adminID: '',
      name: '',
      passwd: '',
    },
    defaultItem: {
      adminID: '',
      name: '',
      passwd: '',
    },

    test_url:"",//测试的url
    test_var1:"",//测试的var1
    test_var2:"",//测试的var2

  }),
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? '新建' : '编辑'
    },
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },

  created () {
    this.initialize()
  },

  methods: {

    // 管理员信息管理
    //获取信息
    getStudent(){
      var that = this;
      var url ="http://127.0.0.1:9090/admin/all";

      axios({
        url: url,
        method: 'get',
      })
          .then(function (response) {
            Object.assign(that.stdData, response.data.data);
            that.stdData.push();
          })
    },
    //更新信息
    updateStudent(){
      var that = this;
      var url ="http://127.0.0.1:9090/admin/update";

      axios({
        url: url,
        method: 'post',
        params:{
          id:that.editedItem.adminID,
          name:that.editedItem.name,
          password:that.editedItem.passwd,
        }
      })
          .then(function (response) {
            alert("更新信息状态" + response.data.msg);
          })

    },
    // 删除管理员信息
    deleteStudent(editedindex){
      var that = this;
      var url ="http://127.0.0.1:9090/admin/delete";

      axios({
        url: url,
        method: 'post',
        params:{
          id:that.stdData[editedindex]['adminID'],
        }
      })
          .then(function (response) {
            alert("删除操作状态：" + response.data.msg);
          })
    },
    //新增管理员信息
    registerStudent(){
      var that = this;
      var url ="http://127.0.0.1:9090/admin/register";

      axios({
        url: url,
        method: 'post',
        params:{
          id:that.editedItem.adminID,
          name:that.editedItem.name,
          password:that.editedItem.passwd,
        }
      })
          .then(function (response) {
            alert("注册操作状态：" + response.data.msg);
          })
    },

    mounted(){
      this.getStudent();
    },
    initialize () {
      this.stdData = [];
    },

    editItem (item) {
      this.editedIndex = this.stdData.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      this.editedIndex = this.stdData.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      this.deleteStudent(this.editedIndex);
      this.stdData.splice(this.editedIndex, 1)
      this.closeDelete()
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.stdData[this.editedIndex], this.editedItem);
      } else {
        this.stdData.push(this.editedItem)
      }
      if (this.editedIndex === -1) {
        this.registerStudent(this.editedIndex);
      }else{
        this.updateStudent(this.editedIndex);
      }
      this.close()
    },
  },
}
</script>

<style scoped>

</style>
